@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400;0,500;0,600;0,700;0,800;0,900;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
.detail__background {
  background-size: cover;
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow-x: hidden;
}

.detail__grid {
  display: grid;
  height: 100%;
  grid-template-columns: repeat(auto-fit, minmax(500px, 1fr));
  padding: 50px 100px;
  place-items: center center;
}

.detail__poster {
  object-position: center center;
  border-radius: 25px;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px,
    rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px,
    rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}

.detail__description {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  grid-gap: 100px;
  gap: 100px;
  padding: 100px 50px;
  text-shadow: 6px 6px 0px rgba(0, 0, 0, 0.2);
}

.detail__title {
  font-size: 5em;
}
.detail__summary {
  font-size: 1.5em;
  line-height: 1.5;
  letter-spacing: 1px;
  text-align: justify;
}

.movies {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
  grid-auto-rows: minmax(100vh, 1fr);
}

.movies a {
  text-decoration: none;
  color: var(--sub--bg--color);
}

.movie {
  background-color: inherit;
  display: flex;
  flex-direction: column;
  transition: all 0.1s linear;
  height: 100%;
}

.movie:hover {
  cursor: pointer;
  background-color: var(--sub--bg--color);
  color: var(--main--bg--color);
  font-style: italic;
}

.movie img {
  height: 60vh;
  object-fit: cover;
  object-position: center center;
}

.movie__data {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 30px;
  grid-gap: 20px;
  gap: 20px;
}

.movie__title {
  font-size: 28px;
  font-weight: 600;
  letter-spacing: 2px;
}

.movie__summary {
  text-align: justify;
  line-height: 1.2;
  overflow: hidden;
}

.movie__genres {
  display: flex;
  justify-content: center;
  grid-gap: 5px;
  gap: 5px;
  flex-wrap: wrap;
}

.genres__genre {
  border: 1px var(--sub--bg--color) solid;
  padding: 5px 10px;
  border-radius: 25px;
  font-size: 14px;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

:root {
  --main--bg--color: #2f2f2f;
  --sub--bg--color: #f2f2f2;
}

body {
  width: 100%;
  -webkit-animation: gradient 15s ease infinite;
          animation: gradient 15s ease infinite;
  color: #f2f2f2;
  color: var(--sub--bg--color);
  font-family: "Playfair Display", serif;
  background-size: 400% 400%;
  background-color: #000000;
  background-image: linear-gradient(45deg, #000000 46%, #ffffff 100%);
}

.container > .loader {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  color: #2f2f2f;
  color: var(--main--bg--color);
}

@-webkit-keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.header {
  z-index: 1;
  height: 50px;
  position: fixed;
  top: 0;
  display: flex;
  align-items: center;
  padding: 50px 50px;
  grid-gap: 20px;
  gap: 20px;
  background-color: inherit;
  font-size: 18px;
  text-transform: uppercase;
}

.header__title {
  border: 1px var(--sub--bg--color) solid;
  border-radius: 50%;
  transition: all 0.1s linear;
  width: 60px;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  text-decoration: none;
  color: var(--sub--bg--color);
}

.header__title:hover {
  cursor: pointer;
  background-color: var(--sub--bg--color);
  color: var(--main--bg--color);
}

